import { useEffect, useState } from "preact/hooks";

import calcHash from "./tkk-hash";

const tkk_url =
  "https://young-dawn-42521.herokuapp.com/translate-proxy/get-tkk";
let ls;
if (typeof window !== "undefined") {
  ls = window.localStorage;
}

async function getTkk(setTkk) {
  var response = await fetch(tkk_url, {
    method: "GET"
  });

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  let { tkk } = await response.json();
  ls.setItem("TKK", tkk);
  ls.setItem("TKK-timestamp", new Date());
  setTkk(tkk);
}

export default function LanguageTools() {
  let [search, setSearch] = useState("");
  let [tkk, setTkk] = useState("");
  let [googleWords, setGoogleWords] = useState([]);

  useEffect(async () => {
    if (
      new Date().valueOf() - new Date(ls.getItem("TKK-timestamp")).valueOf() >
      3600000
    ) {
      getTkk(setTkk);
    } else if (ls.getItem("TKK")) {
      setTkk(ls.getItem("TKK"));
    } else {
      getTkk(setTkk);
    }
  });

  return (
    <div>
      <input
        type="text"
        value={search}
        onInput={event => setSearch(event.target.value)}
      />
      <input
        type="button"
        value="Search"
        onClick={async event => {
          let encoded_search = encodeURIComponent(search);
          let headers = new Headers();
          headers.append("Origin", "https://translate.google.com");
          let response = await fetch(
            `https://young-dawn-42521.herokuapp.com/translate-proxy/get-words?tk=${calcHash(
              search,
              tkk
            )}&q=${encoded_search}`
          );

          let google_response = await response.json();

          let words = [];
          for (let pos of google_response[1]) {
            words.push(pos);
          }
          setGoogleWords(words);
        }}
      />
      <input type="button" value="get tkk" onClick={() => getTkk(setTkk)} />
      <h2>Google Translate</h2>
      {googleWords.map(function(pos) {
        return (
          <div>
            <h3>{pos[0]}</h3>
            {pos[2].map(function(word) {
              let frequency = word[3];
              if (frequency > 0.05) {
                frequency = "common";
              } else if (frequency > 0.01) {
                frequency = "uncommon";
              } else {
                frequency = "rare";
              }
              return (
                <p>
                  {word[0]} - {frequency}
                </p>
              );
            })}
          </div>
        );
      })}
    </div>
  );
}
